	module.exports = {"data":{"post":{"html":"<p>Another half decade, another milestone. Just five years ago I was waxing poetic about <a href=\"/archives/2010/09/01/it-was-twenty-years-ago-today/\">how we had already been in Germany for 20 years</a>, and now it's already been 25 years. Strangely enough my passport from then bears no entry stamp for 1 September 1990.</p>\n<p>At the time our entry into Germany was uncertain. Reunification was in the air, everyone knew it was coming, but the details were still being negotiated behind closed doors. We boarded our plane to Frankfurt on 31 August without knowing our fate. Luckily for us, while our plane was in the air, the \"Einigungsvertrag\" was signed by BRD Interior Minister Wolfgang Sch채uble and DDR State Secretary G체nter Krause, clearing the way for our entry into the soon-to-be unified Germany the next day.</p>\n<p><a href=\"http://www.bundesfinanzministerium.de/Content/DE/Bilder/Bildstrecken/Mediathek/Bildergalerien/Bewegte_Zeiten/Galerie01_11.html\"><img src=\"http://www.bundesfinanzministerium.de/Content/DE/Bilder/Bildstrecken/Mediathek/Bildergalerien/Bewegte_Zeiten/Galerie01_11.jpg?__blob=normal&#x26;v=4\" alt=\"Einigungsvertrag 31-Aug-1990\"></a></p>\n<p>That document did however leave several details open. It never was determined whether our cats would remain American citizens or whether they would obtain German citizenship.</p>","fields":{"title":"25 Years","date":"01 Sep 2015","url":"https://papascott.de/2015-09-01-25-years/"}}},"pathContext":{"slug":"/2015-09-01-25-years/","prev":{"fields":{"title":"Taking your Place","date":"25 May 2015","slug":"/2015-05-25-taking-your-place/"}},"next":{"fields":{"title":"Anniversary","date":"06 Sep 2015","slug":"/2015-09-06-anniversary/"}}}}

